* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #1a1a18;
}

a:focus {
  color: rgb(0, 0, 0)
}

body {
  background-color: #ffffff;
  font-family: raleway-c;
}

@font-face {
  font-family: 'DM-Regular';
  src: url('../fonts/DMSans-Regular.eot'),
    url('../fonts/DMSans-Regular.svg'),
    url('../fonts/DMSans-Regular.ttf'),
    url('../fonts/DMSans-Regular.woff'),
    url('../fonts/DMSans-Regular.woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'DM-Thin';
  src: url('../fonts/DMSans-Thin.eot'),
    url('../fonts/DMSans-Thin.svg'),
    url('../fonts/DMSans-Thin.ttf'),
    url('../fonts/DMSans-Thin.woff'),
    url('../fonts/DMSans-Thin.woff2');
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'NanumGothic';
  src: url('../fonts/NanumGothic.eot'),
    url('../fonts/NanumGothic.svg'),
    url('../fonts/NanumGothic.ttf'),
    url('../fonts/NanumGothic.woff'),
    url('../fonts/NanumGothic.woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.navbar {
  width: 100%;
  background-color: #ffffff;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999; 
  /* border-radius: 0 0 25px 25px; */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, .1);
}

.navbar .container-navbar {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-menu {
  list-style-type: none;
  cursor: pointer;
  padding-left: 0;
  margin-bottom: 0;
  padding: 20px;
  font-family: 'NanumGothic';
}

.navbar-menu li {
  display: inline-block;
  padding: 8px;
}

.navbar-menu a {
  display: inline-block;
  text-decoration: none;
  color: #1a1a18;
}

.navbar-menu li:hover {
  color: #3c4140;
  opacity: 0.8;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.2);
  border-radius: 20px;
}

.link-navbar-logo {
  font-family: 'DM-Thin';
  font-size: 2em;
  text-decoration: none;
  padding: 20px;
  color: black;
}

.main-container {
  margin-top: 10%;
  margin-left: 10%;
  margin-right: 15%;
  height: 70vh;
}

.gallery {
  display: flex;
}

.gallery img {

  max-width: 625px;
  border-radius: 50px;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.5);
}

.main-image {
  width: 45%;
}

.main-text {
  width: 55%;
  margin-top: 2%;
  text-align: left;
}

.hello {
  font-size: 4em;
  font-family: 'DM-Thin';
}

.nickname {
  font-size: 6em;
  font-family: 'DM-Regular';
}

.little-info {
  font-family: 'NanumGothic';
  margin-top: 2em;
}

.print-func {
  padding-top: 3%;
}

.getInTouch {
  margin-top: 5%;
  display: inline-block;
  cursor: pointer;
  font-size: 14px;
  font-family: 'DM-Regular';
  text-decoration: none;
  padding: 7px 60px;
  color: #354251;
  background: #ffffff;
  border-radius: 5px;
  border: 1px solid #000000;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.15);
}

.getInTouch:hover {
  background: #c1d1e0;
  color: #ffffff;
  border: 1px solid #c1d1e0;
  transition: all 0.2s ease;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.5);
}

.Topics {
  margin-top: 10%;
  margin-left: 10%;
  margin-right: 10%;
}

.Topics h2 {
  font-family: 'DM-Regular';
  font-size: 4em;
  margin-bottom: 10px;
}

.text-styles {
  font-family: 'DM-Thin';
  font-size: 2em;
  text-align: justify;
  margin-bottom: 25px;
}

.text-styles b {
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.2);
}

.text-styles-skills {
  font-family: 'NanumGothic';
  font-size: 2em;
  text-align: justify;
  margin-top: 25px;
  margin-bottom: 25px;
  color: #444d4b;
}

.object-lang {
  display: inline-block;
  font-family: 'DM-Regular';
  font-size: 18px;
  text-decoration: none;
  padding: 6px 60px;
  color: #ffffff;
  background: #9eaebd;
  border-radius: 10px;
  margin-top: 10px;
  margin-right: 10px;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.2);
}

.object-lang:hover, .object-tools:hover {
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.4);
  opacity: .5;
}

.object-tools {
  display: inline-block;
  font-family: 'DM-Regular';
  font-size: 18px;
  text-decoration: none;
  padding: 6px 60px;
  color: #ffffff;
  background: #5d6469;
  border-radius: 10px;
  margin-top: 10px;
  margin-right: 10px;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.2);
}

.container-end {
  display: grid;
  grid-template-columns: repeat(3, 350px);
  /* grid-template-rows: repeat(3, 350px); */
  column-gap: 35px;
  row-gap: 35px;
}

.end-button {
  width: 350px;
  height: 350px;
  overflow: hidden;
  border-radius: 50px;
  flex-direction: column;
  justify-content: flex-start;
  border: 0.1px solid #000000;
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.2);
  box-shadow: #000000;
}

.end-button img {
  width: 100%;
  height: 72%;
  object-fit: cover;
}

.end-button:hover {
  box-shadow: 3px 3px 20px rgba(14, 14, 14, 0.4);
}

.end-button .project-text {
  width: 100%;
  height: 30%;
  background-color: #fbfbfb;
  display: flex;
  text-align: left;
  line-height: 1.5;
  font-family: 'NanumGothic';
  font-size: 20px;
  text-decoration: none;
  padding: 10px;
  color: black;
}

.gallery-contact {
  display: flex;
}

.social {
  font-family: 'DM-Thin';
  width: 20%;
}

input, textarea {
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

input[type="submit"] {
  background-color: #586070;
  color: white;
  border: none;
  cursor: pointer;
}


.mini-text {
  font-style: italic;
  font-size: 2em;
  text-align: left;
}

.contact-images {
  width: 50px;
  margin-right: 10px;
  height: 50px;
}

.contact-images-kakaotalk {
  width: 300px;
  height: 350px;
  margin-top: 10px;
  border-radius: 20px;
}

.logotypes {
  margin-top: 10px;
}

footer {
  width: 100%;
  font-family: 'DM-Thin';
  background-color: #f1f1f1;
  height: 20px;
  text-align: center;
  box-shadow: 0px -4px 10px rgba(0, 0, 0, .1);
}

footer a {
  font-style: italic;
}

.logotypes a:hover {
  opacity: 0.8;
}

/* Медиазапрос для планшетных устройств */
@media (max-width: 1024px) {
  .container-end {
    grid-template-columns: repeat(2, 350px);
    grid-template-rows: repeat(2, 350px);
  }
  
  .gallery img {
    margin-right: 10%;
  }

  .social {
    width: 45%;
  }
}

/* Медиазапрос для мобильных телефонов */
@media (max-width: 768px) {
  .container-end {
    grid-template-columns: repeat(1, 350px);
    justify-content: center;
  }

  .link-navbar-logo {
    font-size: 1em;
  }
  .Topics-container {
    margin-top: 100px;
  }

  .gallery {
    display: block;
    width: 100%;
  }

  .gallery-contact {
    display: block;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .social {
    display: block;
    width: 100%;
  }

  .navbar {
    display: none;
  }

  .main-image {
    display: none;
  }

  .getInTouch {
    margin-top: 5%;
    /* margin-bottom: 5%; */
  }

  .contact-images-kakaotalk {
    margin-bottom: 5%;
  }

  .main-text {
    width: 100%;
    margin-top: 20%;
    margin-bottom: 5%;
  }

  .print-func {
    margin-bottom: 5%;
  }
}